html {
  --color-is-active: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.nav__wrapper {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav__logo {
  justify-content: center;
  align-self: center;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-image: none;
  list-style-type: none;
  display: flex;
}

.nav__link {
  text-align: center;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--color-is-active);
}

.nav__list li:first-child .nav__link {
  margin-left: 0;
}

.is-active {
  color: var(--color-is-active);
}

.is-active:after {
  content: "";
  background-color: var(--color-is-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

